<template>
    <div>
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                   border size="mini" v-loading='loading'>
           
            <el-table-column prop="todoType" label="代办类型" align="center" width="100">
                <template slot-scope="scope">
                    {{scope.row.todoType | todoType}}
               </template>
            </el-table-column>
            <el-table-column prop="todoTitle" label="代办提示" align="center" show-overflow-tooltip />
            <!-- <el-table-column prop="todoContent" label="代办其他内容" align="center" show-overflow-tooltip/> -->
            <el-table-column prop="isTodo" label="是否已办理" align="center" width="100">
                <template slot-scope="scope">
                    {{scope.row.isTodo | isTodo}}
               </template>
            </el-table-column>
            <el-table-column prop="todoTime" label="办理时间" align="center" width="150">
               <template slot-scope="scope">
                    {{scope.row.todoTime | formatDate}}
               </template>
            </el-table-column>
           <el-table-column align="center" label="操作" width="130">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeRoleFun(scope.row)" class="el-icon-search btn-link" type="success"><i></i></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="配置" effect="dark" placement="top-end">
                        <el-link @click.stop="editRoleFun(scope.row)" class="el-icon-edit btn-link" type="primary"><i></i></el-link>
                    </el-tooltip>
                    
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getAccountList" class="page-css"/>
        <edit-change :show.sync="object.show" :title="object.title"></edit-change>
    </div>
</template>
<script>
 import {formatDate, YESORNO} from "../../../until/wordBook";
import LjPagination from '../../../components/LjPagination';

 import {apiSysTodoItemsSarch} from '../../../api/case'
 import EditChange from './components/edit';

export default {
   components:{
       EditChange,LjPagination
   },
   data(){
       return{
           list:[{aaa:'sssssssss',bbb:0}],
           loading:true,
           object:{
               title:'',
               show:false,
           },
           form:{
                pageSize: 20,
                pageNum: 1,
           },
           total:0
       }
   },
   methods:{
       //查看
       seeRoleFun(row){
         this.object.title = '查看';
         this.object.show = true ;
       },

       //修改
       editRoleFun(row){
          this.object.title = '修改';
          this.object.show = true ;
       },

       // 查询+搜索
       getAccountList() {
           
            apiSysTodoItemsSarch(this.form).then(res => {
                this.list = res.data;
                this.total = res.total;
                this.list.forEach(item=>{
                    // item.todoContent=JSON.parse(item.todoContent)
                })
                this.loading=false;
            })
       },
   },
   filters:{
      formatDate(val){
          return formatDate(val,'yyyy-MM-dd hh:mm:ss')
      },
      isTodo(val){
          return val==1?'已办理':(val==0?'未办理':'')         
      },
      todoType(val){
          return val==1?'待处理':(val==2?'待审批':'')
      }
   },
   created(){
       this.getAccountList();
       this.loading=false
   }
}
</script>
<style lang="less" scoped>
    .btn-link {
        margin: 0 10px
    }
</style>


